<template>
  <form class="join-form" @submit.prevent="joinProcess">
     <div class="join-box-inner">
         <h3> </h3>
            <div>
            <label for="id"></label>
            <input type="text" id="id" placeholder="ID " v-model="join.user_id" :disabled="validated == true">
            <p :class="id_color">{{id_message}}</p>
            <!-- <label for="nickname"></label>
            <input type="text" id="nickname" placeholder=" " @input="input_nickname" required>
            <p :class="nickname_color">{{nickname_message}}</p> -->
            <label for="pass"></label>
            <input type="password" id="pass" placeholder=" " v-model.lazy="join.user_password" required>
            <label for="pass"> </label>
            <input type="password" id="pass" placeholder="  " v-model.lazy="password_chk" @change="chkPass" required>
            <p :class="password_color">{{password_message}}</p>
            <label for="email"> </label>
            <input type="text" v-model.lazy="join.user_email" id="email" placeholder="  " required>
            <p :class="email_color">{{email_message}}</p>
            <div class="btn-group">
                <button class="btn btn-primary"> </button>
            </div>
         </div>
     </div>
  </form>
</template>

<script>
import axios from '@/setting/axiossetting.js';
import {watch,ref} from 'vue';
import router from '@/router';
import { useRoute } from 'vue-router';

export default {
    setup(){
        const kakaoId= useRoute().params.id;
        const validated = ref(false);
        //  .
        const join = ref({
            user_id : '',
            user_password : '',
            user_nickname:'',
            user_email:''
        });

        if(kakaoId != undefined){
            join.value.user_id = "kakao" + kakaoId;
            validated.value =true;
            axios.get()
        }
        else validated.value=false;
        //   .
        const password_chk = ref('');
        const password_message = ref('');
        const password_color = ref('');
        const id_message = ref('');
        const id_color = ref('');
        // const nickname_message = ref('');
        // const nickname_color=ref('');
        const email_message = ref('');
        const email_color = ref('');

        const chkPass= (e)=>{
            if(e.target.value != join.value.user_password && join.value.user_password != ''){
                password_message.value="  .";
                password_color.value = "red";
            }else if(e.target.value == join.value.user_password){
                password_message.value="  .";
                password_color.value="green";
            }
        }//   end

        const idcheck = async() =>{
            try{
                console.log("id check=" + join.value.user_id);

                const data = await( await axios.get("users/idcheck",{
                    params:{
                        user_id : join.value.user_id
                    }
                })).data;
                if(data){
                    id_message.value="  .";
                    id_color.value ="green";

                }else{
                    id_message.value=" .   .";
                    id_color.value="red";
                }
            }catch(err){
               id_message.value="   .  .  .";
            }
        };
        
        
        //  
        // const input_nickname = (e) =>{
        //     join.value.user_nickname = e.target.value;
        // }// @input  

        // const nicknamecheck = async() =>{

        //     try{
        //     const data = await( await axios.get("users/nickname",{
        //                     params:{
        //                         user_nickname : join.value.user_nickname
        //                     }
        //                 })).data;
        //                 if(data){
        //                     nickname_message.value = "  .";
        //                     nickname_color.value = "green";
        //                 }else{
        //                         nickname_message.value = " .   .";
        //                         nickname_color.value = "red";
        //                 }
        //     }catch(err){
        //          nickname_message.value="   .  .  .";
        //     }
              
        // }//nicknamecheck  

        const joinProcess = async() =>{
            if(id_color.value=="red"){
                alert(" ");
            }
            else if(email_color.value=="red"){
                alert("email ");
            }else{
                try{
                    const res = await axios.post('users/add',join.value);
                    if(res.data==1){
                        alert(' .')
                         router.push({
                            name:'MailAuth',
                            params: {id: join.value.user_id}
                        })
                    }
                }catch(err){
                    console.log(err)
                }
            }
        }
        //watch   
         watch(
           ()=>join.value.user_id,(next,prev) =>{
               console.log('watch(user_id)' + prev + "=>" + next)
               const pattern = /^\w{5,30}$/;
               if(!pattern.test(join.value.user_id)){
                   id_message.value ='  _ 5~30 .';
                   id_color.value='red';
               }else{
                   idcheck();
               }
           });//  
        // watch(()=>join.value.user_nickname,()=>{
        //     const pattern = /^\w{5,10}$/;
        //     if(!pattern.test(join.value.user_nickname)){
        //         nickname_message.value ="   5~10 .";
        //         nickname_color.value="red";
        //     }else{
        //         nicknamecheck();
        //     }
        // })//  
        watch(()=>join.value.user_password,()=>{
            const pattern = /^[0-9a-zA-Z]{8,20}$/;
            if(!pattern.test(join.value.user_password)){
                password_message.value=" 5~20 , ";
                password_color.value = "red";
            }
        })//  
        // watch(()=>join.value.user_nickname,()=>{
        //     const pattern = /^[a-zA-Z0-9-|-|-]{5,10}$/;
        //     if(!pattern.test(join.value.user_nickname)){
        //         nickname_message.value=" ,, 5~10  .";
        //         nickname_color.value ="green";
        //     }
        // })//  

        watch(()=> join.value.user_email,()=>{
            const pattern = /^\w+@\w+[.]\w{3}$/;
            console.log(join.value.user_email);
            if(!pattern.test(join.value.user_email)){
                email_message.value ="   .";
                email_color.value='red';
            }else{
                email_message.value="  ."
                email_color.value="green";
            }
        })//  
        return{
            chkPass,join,password_chk,id_message,id_color,password_message,password_color
            ,email_message,email_color,joinProcess,validated
        }
    }
}
</script>

<style lang="scss" scoped>
.red{
    color:red;
}
.green{
    color:green;
}

  .join-form{
    margin : 0 auto;
    max-width: 400px;
    .join-box-inner{
        margin-bottom: 150px;
        p{
            margin:0px;
            font-weight: bold;

        }
        width: 350px;
        padding: 15px 20px;
        border-radius: 5px;
        border : 1px solid rgba(0,0,0,0.2);
        input{ 
            min-width:100%;
            padding: 3px 7px;
            border-radius: 2px;
            height:31px;
            font-size:12px;
            border : 1px solid rgba(0,0,0,0.4);
            margin-bottom: 5px;
        }
        input:focus{
            font-size:15px;
            
        }
        input::placeholder{
            font-size:12px
        }
        input::-moz-placeholder{
            font-size:12px;
        }
        label{
            padding: 0 0 2px 2px;
            margin-bottom: 5px;
        }
        .btn-group{
            margin-top:10px;
            width:100%;
            button{
                width:50px;
                float: right;
            }
        }
    }

    
  }
</style>